.vc__button {
  display: flex;
  margin: auto;
  border-radius: 50%;
  background: var(--header_background);
  width: 300px;
  height: 300px;
  align-items: center;
  justify-content: center;
  -webkit-box-shadow: 0 6px 15px 0 rgba(0, 0, 0, 0.7);
  box-shadow: 0 6px 15px 0 rgba(0, 0, 0, 0.7);
  cursor: pointer;
  overflow: hidden;
}
.vc__button svg {
  border-radius: 50%;
}
.vc__button:active {
  transform: scale(0.96);
}
